.main__promo-block {
  display: grid;
  grid-template-rows: -webkit-min-content;
  grid-template-rows: min-content;
  grid-template-columns: 300px 300px 300px;
  gap: 20px;
  max-width: 960px;
  margin: 59px auto;
  padding: 0 10px;
}
.promo-list {
  display: contents;
  padding: 0;
  margin: 0;
}
.main-slider {
  position: relative;
  grid-row-start: 2;
  grid-row-end: 4;
  grid-column-start: 1;
  grid-column-end: 3;
  background-color: black;
  background-image: url("/src/img/Layer_99.jpg");
  background-repeat: no-repeat;
  color: white;
}
.main-slider a {
  display: block;
  width: 195px;
  text-decoration: none;
  text-align: center;
  color: white;
  margin-top: 116px;
  margin-left: 25px;
  padding: 10px;
  background: #ee3643;
}
.main-slider h3 {
  text-transform: uppercase;
  font-size: 36px;
  font-weight: 700;
  margin: 0;
  padding: 25px 0 0 24px;
}
.main-slider p {
  font-size: 18px;
  padding-left: 24px;
  margin: 0;
  margin-top: 5px;
}
.promo {
  position: relative;
  overflow: hidden;
  width: 300px;
  color: white;
  margin: 0;
  padding-top: 10px;
  padding-left: 20px;
  min-height: 123px;
}
.promo h2 {
  margin: 10px 0 0 0;
}
.promo a {
  display: block;
  margin-top: 15px;
  padding: 10px;
  min-height: 38px;
  text-align: center;
  color: white;
  text-transform: uppercase;
  width: 135px;
  background-color: rgba(255, 255, 255, 0.2);
}
.promo a:hover {
  background-color: rgba(255, 255, 255, 0.3);
}
.promo a:focus {
  background-color: rgba(255, 255, 255, 0.4);
}
.promo__matherial {
  background-color: #ffbf47;
  background-image: url("/src/img/icon-1.png");
  background-repeat: no-repeat;
  background-position: 85%;
}
.promo__tools {
  background-color: #3bbce3;
  background-image: url("/src/img/icon-2.png");
  background-repeat: no-repeat;
  background-position: 85%;
}
.promo__technica {
  background-color: #dc91d8;
  background-image: url("/src/img/icon-3.png");
  background-repeat: no-repeat;
  background-position: 85%;
}
.promo__discount {
  background-color: #8ed78f;
  background-image: url("/src/img/icon-4.png");
  background-repeat: no-repeat;
  background-position: 85%;
}
.promo__free {
  background-color: #ffc047;
  background-image: url("/src/img/icon-5.png");
  background-repeat: no-repeat;
  background-position: 85%;
}
.banner-new {
  position: absolute;
  right: -40px;
  top: -5px;
  min-height: 30px;
  padding: 8px;
  width: 120px;
  font-size: 14px;
  background-color: #ff5357;
