  color: #ee3643;
  text-align: center;
  padding: 8px 54px 8px 74px;
  font-size: 21px;
  line-height: 21px;
  margin: 0;
  margin-bottom: 2px;
}
.telephone__number::before {
  content: "";
  position: absolute;
  width: 19px;
  height: 19px;
  background: url("/src/img/icon-phone.png");
  left: 15px;
}
.info__adress {
  margin: 0 auto;
}
.info__login {
  position: relative;
}
.login-btn {
  background: white;
  border: none;
  width: 120px;
  height: 45px;
  padding-left: 20px;
}
.login-btn::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 17px;
  opacity: 0.3;
  background: url("/src/img/icon-login.svg");
  left: 14px;
}
.login-btn:hover {
  color: #ee3643;
}
.login-btn:hover::before {
  opacity: 1;
}
.register-btn:hover {
  color: #ee3643;
}
.register-btn {
  background: white;
  border: none;
  width: 120px;
  height: 45px;
}
.header-navigation {
  margin-top: 44px;
  margin-bottom: 60px;
}
.main-navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 960px;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 0 20px;
  background-color: #32425c;
  min-height: 60px;
  box-shadow: inset 0 -5px 0 #293449;
}
.main-navigation li {
  text-align: center;
  font-size: 17px;
  list-style-type: none;
}
.main-navigation li a {
  display: block;
  text-decoration: none;
  color: white;
  padding-top: 16px;
  padding-bottom: 20px;
  padding-left: 24px;
  padding-right: 24px;
}
.main-navigation li:hover {
  background-color: #293449;
}
.page {
  max-width: 960px;
  margin: 40px auto;
}
.empty-page {
  text-transform: uppercase;
  font-size: 26px;
  width: 960px;
  padding: 5px 20px;
  background: #f3f7f9;
}
.header-logo:focus {
  outline: none;
  box-shadow: inset 0 -5px 0 rgba(0, 0, 0, 0.24);
  background-color: #ba2732;
}
.scan-input:focus + .scan-icon::before {
  position: absolute;
  content: "";
  width: 17px;
  height: 17px;
  top: 50%;
  left: 17px;
  transform: translateY(-50%);
  opacity: 1;
  background: url("/src/img/icon-search2.png") no-repeat;
}
.scan-input:focus {
  color: black;
  background-color: white;
}
.nav-button:focus {
  outline: none;
  background-color: #161d29;
}
.nav-button__green:focus {
  background-color: #518534;
}
.navigation__button:focus {
  outline: none;
  background: #923137;
}
